<template>
    <div>
        <form @submit="addTodo">
            <input type="text" v-model="title" name="title" placeholder="Add Todo...">
            <input type="submit" value="Submit" class="btn">
        </form>
    </div>
</template>

<script>
export default {
    name: "AddTodo",
    data() {
        return {
            title: "",
            completed: false
        }
    }, 
    methods: {
        addTodo(e) {
            e.preventDefault();

            const newTodo = {
                title: this.title,
                completed: false
            }

            //send to parent
            this.$emit("add-todo", newTodo);

            this.title = "";
        }
    }
}
</script>

<style>
    form {
        display: flex;
    }

    input[type="text"] {
        flex: 10;
        padding: 5px;
    }
</style>
